import Vue from 'vue'
import Router from 'vue-router'
import indexPage from "../pages/index.vue"
import bookPage from "../pages/book.vue"
import readerPage from "../pages/reader.vue"
import searchPage from "../pages/search.vue"
import femalePage from "../pages/female.vue"
import malePage from "../pages/male.vue"
Vue.use(Router)

export default new Router({
  mode:"history",
  base:'/webbook/',
  routes: [
    {
      name:'index',
      path:'/',
      component:indexPage,
      meta:{
        title:'',
        width:null,
        keepAlive: true,
        scrollTop:null
      }
    },
    {
      name:'book',
      path:'/book',
      component:bookPage,
      meta:{
        title:'',
        width:null,
        keepAlive:false,
        pathName:'index'
      }
    },
    {
      name:'reader',
      path:'/reader',
      component:readerPage,
      meta:{
        width:null,
        pathName:'index'
      }
    },
    {
      name:'search',
      path:'/search',
      component:searchPage,
      meta:{
        title:'',
        width:null,
        keepAlive: false
      }
    },
    {
      name:'female',
      path:'/female',
      component:femalePage,
      meta:{
        title:'',
        width:null,
        keepAlive:false,
        scrollTop:null
      }
    },
    {
      name:'male',
      path:'/male',
      component:malePage,
      meta:{
        title:'',
        width:null,
        scrollTop:null,
        keepAlive:false
      }
    },
  ]
})



